import { useState } from 'react'

import AddTaskIcon from '@mui/icons-material/AddTask'
import CheckBoxIcon from '@mui/icons-material/CheckBox'
import CheckBoxOutlineBlankIcon from '@mui/icons-material/CheckBoxOutlineBlank'
import DoneAllIcon from '@mui/icons-material/DoneAll'
import FormatListBulletedIcon from '@mui/icons-material/FormatListBulleted'
import HighlightOffRoundedIcon from '@mui/icons-material/HighlightOffRounded'
import LooksOneOutlinedIcon from '@mui/icons-material/LooksOneOutlined'
import TextFieldsIcon from '@mui/icons-material/TextFields'
import type { ButtonProps } from '@mui/material'
import { Box, IconButton, TextField, Typography, useTheme } from '@mui/material'

import type { QuestionEdit } from 'src/models/form'

import CheckBoxChoice from 'src/content/manage/Recruit/RecruitQuestion/CheckBoxChoice'

const CHARACTER_LIMIT_COUNT = 500
const OPTIONS_LIMIT_COUNT = 20

interface QuestionBoxProps {
  index: number
  question: QuestionEdit
  handleChangeQuestionType: (questionId: number) => React.MouseEventHandler<HTMLButtonElement>
  handleCheckBoxType: (questionId: number) => React.MouseEventHandler<HTMLButtonElement>
  handleDeleteQuestion: (questionId: number) => React.MouseEventHandler<HTMLButtonElement>
  handleChangeIsEssential: (questionId: number) => React.MouseEventHandler<HTMLButtonElement>
  handleQuestionFieldChange: React.ChangeEventHandler<HTMLTextAreaElement>
  handleOptionFieldChangeSync: (questionId: number, options: string[]) => void
}

const QuestionBox: React.FC<QuestionBoxProps> = ({
  index,
  question,
  handleDeleteQuestion,
  handleQuestionFieldChange,
  handleChangeIsEssential,
  handleChangeQuestionType,
  handleCheckBoxType,
  handleOptionFieldChangeSync
}) => {
  const theme = useTheme()
  const { spacing } = theme

  const { viewId, contents, isCheckBox, options, isEssential, isMultiSelectable } = question
  const [optionData, setOptionData] = useState<string[]>(options || [])

  const handleOptionFieldChange: React.ChangeEventHandler<HTMLTextAreaElement> = ({ target: { name, value } }) => {
    const optionIndex = Number(name)
    setOptionData(prev => prev.map((option, index) => (index === optionIndex ? value : option)))
    handleOptionFieldChangeSync(viewId, optionData)
  }

  const handleAddOption: React.MouseEventHandler<HTMLButtonElement> = () => {
    if (optionData.length >= OPTIONS_LIMIT_COUNT) return
    setOptionData(prev => [...prev, ''])
  }

  const handleDeleteOption: React.MouseEventHandler<HTMLButtonElement> = ({ currentTarget }) => {
    const optionIndex = Number(currentTarget.name)
    setOptionData(prev => prev.filter((_, index) => index !== optionIndex))
  }

  return (
    <Box
      position="relative"
      sx={{ border: 1, borderRadius: 2, borderColor: theme.palette.grey[300], padding: 2, mb: 2 }}
    >
      <Typography variant="h6" sx={{ marginBottom: spacing(0.5), textAlign: 'left' }}>
        질문 {index + 1}
      </Typography>
      <TextField
        name={String(viewId)}
        onChange={handleQuestionFieldChange}
        variant="standard"
        placeholder="질문을 입력해주세요"
        value={contents}
        helperText={`${contents.length} / ${CHARACTER_LIMIT_COUNT}`}
        inputProps={{ maxLength: CHARACTER_LIMIT_COUNT }}
        multiline
        fullWidth
        maxRows={4}
        required
        sx={{
          margin: spacing(1, 0),
          padding: spacing(1, 0),
          '& label': { fontSize: '1.25rem' },
          '& .MuiFormHelperText-root': { textAlign: 'right' }
        }}
      />
      {isCheckBox && optionData && (
        <>
          {optionData.map((option, index) => (
            <CheckBoxChoice
              key={index}
              index={index}
              choice={option}
              onChange={handleOptionFieldChange}
              handleDelete={handleDeleteOption}
            />
          ))}
          <IconButton
            sx={{ maxWith: 500, pl: 0, display: 'block' }}
            onClick={handleAddOption}
            disabled={options?.length === OPTIONS_LIMIT_COUNT}
          >
            <AddTaskIcon fontSize="small" />
            <Typography variant="button" mx={2}>
              &nbsp;선택지 추가
            </Typography>
            <Typography variant="caption" color="text.secondary">
              {optionData.length}/{OPTIONS_LIMIT_COUNT}
            </Typography>
          </IconButton>
        </>
      )}

      <Box display={'flex'} flexDirection={'row'} justifyContent={'flex-end'}>
        <ChangeIsEssentialButton onClick={handleChangeIsEssential(viewId)} isEssential={isEssential} />
        {isCheckBox && (
          <ChangeCheckBoxTypeButton onClick={handleCheckBoxType(viewId)} isMultiSelectable={isMultiSelectable} />
        )}
        <ChangeQuestionTypeButton onClick={handleChangeQuestionType(viewId)} isCheckBox={isCheckBox} />
        <DeleteQuestionButton onClick={handleDeleteQuestion(viewId)} />
      </Box>
    </Box>
  )
}

export default QuestionBox

type QuestionButtonProps = Pick<ButtonProps, 'onClick'>

const DeleteQuestionButton: React.FC<QuestionButtonProps> = ({ onClick }) => {
  return (
    <IconButton type="button" aria-label="질문 삭제하기" color="error" onClick={onClick}>
      <HighlightOffRoundedIcon fontSize="small" />
      <Typography variant="button">&nbsp;삭제</Typography>
    </IconButton>
  )
}

interface ChangeQuestionTypeButtonProps extends QuestionButtonProps {
  isCheckBox: boolean
}

const ChangeQuestionTypeButton: React.FC<ChangeQuestionTypeButtonProps> = ({ onClick, isCheckBox }) => {
  return (
    <IconButton type="button" aria-label="질문 유형 변경하기" onClick={onClick}>
      {isCheckBox ? <FormatListBulletedIcon fontSize="small" /> : <TextFieldsIcon fontSize="small" />}
      <Typography variant="button">&nbsp;{isCheckBox ? '선택형' : '장문형'}</Typography>
    </IconButton>
  )
}

interface ChangeCheckBoxTypeButtonProps extends QuestionButtonProps {
  isMultiSelectable: boolean
}

const ChangeCheckBoxTypeButton: React.FC<ChangeCheckBoxTypeButtonProps> = ({ onClick, isMultiSelectable }) => {
  return (
    <IconButton type="button" aria-label="선택 개수 변경하기" onClick={onClick}>
      {isMultiSelectable ? <DoneAllIcon fontSize="small" /> : <LooksOneOutlinedIcon fontSize="small" />}
      <Typography variant="button">&nbsp;{isMultiSelectable ? '다중선택' : '단일선택'}</Typography>
    </IconButton>
  )
}

interface ChangeIsRequiredButtonProps extends QuestionButtonProps {
  isEssential: boolean
}

const ChangeIsEssentialButton: React.FC<ChangeIsRequiredButtonProps> = ({ onClick, isEssential }) => {
  return (
    <IconButton type="button" aria-label="필수 여부 변경하기" onClick={onClick}>
      {isEssential ? <CheckBoxIcon fontSize="small" /> : <CheckBoxOutlineBlankIcon fontSize="small" />}
      <Typography variant="button">&nbsp;필수항목</Typography>
    </IconButton>
  )
}
