import React, { useEffect, useRef, useState } from 'react'
import { Helmet } from 'react-helmet-async'
import { useNavigate, useParams } from 'react-router'

import ControlPointRoundedIcon from '@mui/icons-material/ControlPointRounded'
import type { ButtonProps } from '@mui/material'
import { Box, Button, Card, Container, IconButton, Stack, Typography } from '@mui/material'

import type { QuestionEdits, Questions } from 'src/models/form'

import QuestionBox from 'src/content/manage/Recruit/RecruitQuestion/QuestionBox'

import { useAddQuestion, useGetQuestions } from 'src/hooks/api/forms'

const QUESTION_COUNT = {
  MIN: 0,
  MAX: 7
}

const defaultQuestionTemplate = {
  contents: '',
  isCheckBox: false,
  isMultiSelectable: false,
  viewId: 0,
  isEssential: true
}

const Form: React.FC = () => {
  const { id, recruitId } = useParams()
  const clubId = Number(id)
  const recruitIdNumber = Number(recruitId)

  const navigate = useNavigate()
  const nextQuestionIdRef = useRef(1)

  const [questions, setQuestions] = useState<QuestionEdits>([defaultQuestionTemplate])

  const { data: questionData } = useGetQuestions(clubId, recruitIdNumber)
  const addQuestions = useAddQuestion()

  useEffect(() => {
    if (questionData) {
      if (questionData.every(question => question.contents.length === 0)) {
        return
      }
      setQuestions(
        questionData.map(question => ({
          ...question,
          viewId: question.id
        }))
      )
      nextQuestionIdRef.current = questionData.length + 1
      return
    }
    setQuestions([])
  }, [questionData])

  const handleQuestionFieldChange: React.ChangeEventHandler<HTMLTextAreaElement> = ({ target: { name, value } }) => {
    const questionId = Number(name)
    const currentIndex = questions.findIndex(({ viewId }) => viewId === questionId)

    setQuestions(prev => {
      const nextQuestions = [...prev]
      nextQuestions[currentIndex].contents = value
      return nextQuestions
    })

    setQuestions(
      questions.map((question, index) => (index === currentIndex ? { ...question, contents: value } : question))
    )
  }

  const handleAddQuestionButtonClick: React.MouseEventHandler<HTMLButtonElement> = () => {
    if (questions.length >= QUESTION_COUNT.MAX) return
    setQuestions([...questions, { ...defaultQuestionTemplate, viewId: nextQuestionIdRef.current }])
    nextQuestionIdRef.current += 1
  }

  const handleDeleteQuestionButtonClick: (questionId: number) => React.MouseEventHandler<HTMLButtonElement> =
    questionId => () => {
      setQuestions(prev => prev.filter(({ viewId }) => viewId !== questionId))
    }

  const handleSubmit: React.FormEventHandler<HTMLFormElement> = async e => {
    e.preventDefault()

    // eslint-disable-next-line @typescript-eslint/no-unused-vars
    const modifiedQuestions: Questions = questions.map(({ contents, viewId, options, isCheckBox, ...rest }) => ({
      contents: contents.trim(),
      options: isCheckBox ? options?.map(option => option.trim()) : [],
      isCheckBox,
      ...rest
    }))

    addQuestions.mutate({ clubId, recruitId: recruitIdNumber, questions: modifiedQuestions })
    navigate(`/manage/${clubId}/recruit`)
  }

  const handleCancelButtonClick: React.MouseEventHandler<HTMLButtonElement> = () => {
    navigate(`/manage/${clubId}/recruit`)
  }

  const handleChangeQuestionType: (questionId: number) => React.MouseEventHandler<HTMLButtonElement> =
    questionId => () => {
      setQuestions(prev =>
        prev.map(question =>
          question.viewId === questionId
            ? {
                ...question,
                isCheckBox: !question.isCheckBox
              }
            : question
        )
      )
    }

  const handleCheckBoxType: (questionId: number) => React.MouseEventHandler<HTMLButtonElement> = questionId => () => {
    setQuestions(prev =>
      prev.map(question =>
        question.viewId === questionId
          ? {
              ...question,
              isMultiSelectable: !question.isMultiSelectable
            }
          : question
      )
    )
  }

  const handleChangeIsEssential: (questionId: number) => React.MouseEventHandler<HTMLButtonElement> =
    questionId => () => {
      setQuestions(prev =>
        prev.map(question =>
          question.viewId === questionId
            ? {
                ...question,
                isEssential: !question.isEssential
              }
            : question
        )
      )
    }

  const handleOptionFieldChangeSync = (viewId: number, options: string[]) => {
    setQuestions(prev =>
      prev.map(question =>
        question.viewId === viewId
          ? {
              ...question,
              options
            }
          : question
      )
    )
  }

  return (
    <Container
      maxWidth={'lg'}
      sx={{
        minHeight: '70vh',
        display: 'flex',
        justifyContent: 'center',
        marginTop: 10
      }}
    >
      <Helmet>
        <title>신청폼 관리</title>
      </Helmet>
      <Container maxWidth="lg">
        <Box component="form" onSubmit={handleSubmit}>
          <Card>
            <Box p={{ xs: 2, md: 5 }}>
              <Typography variant="h2">신청폼 관리</Typography>
              <Typography variant="subtitle1">
                {`${QUESTION_COUNT.MIN}개부터 ${QUESTION_COUNT.MAX}개까지 작성할 수 있습니다.`}
              </Typography>
              <Box mt={3} sx={{ display: 'flex', flexDirection: 'column' }}>
                {questions.map((question, index) => (
                  <QuestionBox
                    key={index}
                    question={question}
                    index={index}
                    handleChangeQuestionType={handleChangeQuestionType}
                    handleCheckBoxType={handleCheckBoxType}
                    handleDeleteQuestion={handleDeleteQuestionButtonClick}
                    handleQuestionFieldChange={handleQuestionFieldChange}
                    handleChangeIsEssential={handleChangeIsEssential}
                    handleOptionFieldChangeSync={handleOptionFieldChangeSync}
                  />
                ))}
                <AddQuestionButton
                  onClick={handleAddQuestionButtonClick}
                  disabled={questions.length >= QUESTION_COUNT.MAX}
                />
              </Box>
            </Box>
          </Card>
          <Stack mt={2} pb={2} direction="row" spacing={2} justifyContent="center">
            <Button
              type="button"
              variant="outlined"
              onClick={handleCancelButtonClick}
              sx={{ width: '50%', maxWidth: '120px' }}
            >
              뒤로가기
            </Button>
            <Button type="submit" variant="contained" sx={{ width: '50%', maxWidth: '120px' }}>
              질문 수정
            </Button>
          </Stack>
        </Box>
      </Container>
    </Container>
  )
}

export default Form

type AddQuestionButtonProps = Pick<ButtonProps, 'onClick' | 'disabled'>

const AddQuestionButton: React.FC<AddQuestionButtonProps> = ({ onClick, disabled }) => {
  return (
    <IconButton
      type="button"
      aria-label="질문 추가하기"
      color="primary"
      onClick={onClick}
      disabled={disabled}
      sx={{ margin: '0 auto', justifySelf: 'center' }}
    >
      <ControlPointRoundedIcon fontSize="small" />
      <Typography variant="button">&nbsp;질문 추가하기</Typography>
    </IconButton>
  )
}
