import DeleteOutline from '@mui/icons-material/DeleteOutline'
import RadioButtonUncheckedIcon from '@mui/icons-material/RadioButtonUnchecked'
import { Box, IconButton, TextField } from '@mui/material'

interface CheckBoxChoiceProps {
  choice: string
  index: number
  onChange: React.ChangeEventHandler<HTMLTextAreaElement>
  handleDelete: React.MouseEventHandler<HTMLButtonElement>
}

const CheckBoxChoice = ({ index, choice, onChange, handleDelete }: CheckBoxChoiceProps) => {

  return (
    <Box sx={{ display: 'flex', alignItems: 'center' }}>
      <RadioButtonUncheckedIcon fontSize="small" />
      <TextField
        name={String(index)}
        onChange={onChange}
        variant="standard"
        value={choice}
        required
        sx={{ ml: 2, maxWidth: 500 }}
        placeholder="최대 30자"
      />
      <IconButton sx={{ ml: 2 }} onClick={handleDelete} name={`${index}`}>
        <DeleteOutline fontSize="small" color={'disabled'} />
      </IconButton>
    </Box>
  )
}

export default CheckBoxChoice
